    .forge-jumbotron .container {
        margin-left: 20px;
        margin-right: 20px;
        min-height: 250px;
    }
}

@media (max-width: 768px) {
    .fullscreen .forge-jumbotron .container {
        height: 100vh;
        width: 90vw;
    }
}

@media (max-width: 768px) {
    .explode .forge-jumbotron .range-style {
        width: 50%;
    }
}

@media (max-width: 768px) {
